import React from 'react';
import ReactDOM from "react-dom";
import { Route, BrowserRouter as Router } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.css';
import "../src/assets/css/main.scss"
import * as serviceWorker from './serviceWorker';
import Login from './pages/login';
import Home from './pages/Home';
import Register from './pages/register';
import MouduleDetail from "./pages/Course/moduleDetail";
import Quiz from "./pages/Course/quiz";
import QuizResult from "./pages/Course/quizResult";
import UserManagement from "./pages/Admin/UserManagement";

const routing = (
  <Router>
      <Route path="/" component={Home} exact/>
      <Route path="/login" component={Login} exact/>
      <Route path="/registration" component={Register} exact/>
      <Route path="/course/module-detail" component={MouduleDetail} exact/>
      <Route path="/course/quiz" component={Quiz} exact/>
      <Route path="/course/quiz/result" component={QuizResult} exact/>
      {/* Admin */}
      <Route path="/admin/user-management" component={UserManagement} exact/>
  </Router>
)
ReactDOM.render(routing, document.getElementById("root"));
// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
