import React , {useEffect, useState} from 'react'
import Logo from '../Common/Logo'
import { Link } from 'react-router-dom'
import CartIcon from '../../assets/images/icons/cart.svg'

const Header = () => {
    
    const [isSticky, setSticky] = useState(false);
    const [isCollapsed , setCollapsed] = useState(null);
  
    useEffect(() => {
      window.addEventListener("scroll", () => {
        if(window.scrollY > 50){
          setSticky(true)
        }else{
          setSticky(false)
        }
      })
    }, []);
    return (
        <nav className={ (isSticky || isCollapsed) ? "home-nav slide in show shadow-sm navbar navbar-expand-sm sticky-bg navbar-light text-center fixed-top" : "home-nav slide out show navbar navbar-expand-sm navbar-light py-4 fixed-top text-center "}>
            <div className="container">
                <Logo/>
                <button onClick={
                    () => setCollapsed(!isCollapsed ? 'show' : null )} className="navbar-toggler d-lg-none" type="button" data-toggle="collapse" data-target="#collapsibleNavId" aria-controls="collapsibleNavId"
                    aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="collapsibleNavId">
                    <ul className="navbar-nav ml-auto mt-2 mt-lg-0">
                        <li className="nav-item active">
                            <Link className="nav-link" to="/">Home</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/course">Course</Link>
                        </li>
                        <li className="nav-item dropdown">
                            <Link className="nav-link" to="/cart">
                                <img className="cart-icon" src={CartIcon} alt="cart"></img>
                            </Link>
                        </li>
                    </ul>
                </div>
            </div>

        </nav>
    )
}

export default Header
