import React from "react";
import unCheckIcon from "../../../assets/images/icons/uncheck-box.png";
import checkIcon from "../../../assets/images/icons/check-box.png";

const Option = (props) => {
    return (
        <div className="option-component">
            <div className="pb-2 pt-2">
                <button
                    onClick={() => props.handleChoose(props.choice.id)}
                    className={`btn btn-block ${props.choose.id === props.choice.id && props.choose.status ? `btn-success chosen-button` : ""}`}>
                    <div class="d-flex">
                        <img className="mr-auto p-2 chosen-button-icon" src={
                            props.choose.id === props.choice.id && props.choose.status 
                            ? 
                            checkIcon 
                            : 
                            unCheckIcon} />
                        <div className="mr-auto p-2">{props.choice.title}</div>
                    </div>
                </button>
            </div>
        </div>

    )
}

export default Option;