import React, { useState } from 'react'
import CourseLearning from "./CourseLearning";
import CourseObjective from "./CourseObjective";

const CourseInformation = () => {
    const [menuButton, setMenuButton] = useState({
        objective: true,
        learning: false
    });

    const handleOnClick = (pressed) => {
        setMenuButton({
            [pressed]: true
        })
    }

    return (
        <section className="course-information-component">
            <div className="container h-100">
                <div className="d-flex justify-content-center">
                    <div className="course-information-heading-div d-flex justify-content-between align-items-center">
                        <div className={menuButton.objective && "course-information-title"} onClick={() => handleOnClick("objective")}>এই কোর্সের বিশেষত্ব কি ?</div>
                        <div className={menuButton.learning && "course-information-title"} onClick={() => handleOnClick("learning")}>এই কোর্সে কি কি শিখতে পারবে</div>
                    </div>
                </div>
                {menuButton.objective ?
                    <CourseObjective />
                    :
                    <CourseLearning />
                }
            </div>
        </section>
    )
}

export default CourseInformation;
