import React, { useState, Fragment } from "react";
import Accordion from '@material-ui/core/Accordion';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus, faMinus, faAngleUp } from '@fortawesome/free-solid-svg-icons';


const CustomAccordion = (props) => {
    const [expand, setExpand] = useState(false);

    const handleOnClick = () => {
        setExpand(!expand);
    }
    return (
        <div className="custom-accordion-component shadow">
            <Accordion>
                <AccordionSummary
                    onClick={() => handleOnClick()}
                    expandIcon={
                        props.icon === "module"
                            ?
                            <FontAwesomeIcon icon={faAngleUp} />
                            :
                            <FontAwesomeIcon icon={expand ? faMinus : faPlus} />}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                >
                    <div>
                        <h5 className="milestone-title mb-0">{props.title}</h5>
                        {props.minute || props.modulePart ?
                            <div className="d-flex flex-row">
                                <p className="mb-0 mr-2 sub-milestone-title">{props.minute}min &#9679;</p>
                                <p className="sub-milestone-title">{props.modulePart}</p>
                            </div>
                            : <Fragment></Fragment>}
                    </div>
                </AccordionSummary>
                <AccordionDetails>
                    {props.children}
                </AccordionDetails>
            </Accordion>
        </div>
    )
}

export default CustomAccordion;