import React from "react";
import { Link } from "react-router-dom";
import manWithLaptop from "../../../assets/images/man-with-laptop.png";


const LoginRegisterForm = (props) => {

    const handleOnSubmit = (event) => {
        event.preventDefault();
    }

    return (
        <div className="row login-register-form">
            <div className="col-md-6 pr-md-5 my-2">
                <form className="auth-form p-5 bg-white">
                    <h2 className="mb-4">{props.label}</h2>
                    {props.label === "Register" &&
                        <div className="form-group">
                            <label htmlFor="name">Name</label>
                            <input id="name" type="text" className="form-control" />
                        </div>
                    }
                    <div className="form-group">
                        <label htmlFor="email">Email </label>
                        <input id="email" type="text" className="form-control" />
                    </div>
                    {props.label === "Register" &&
                        <div class="form-group">
                            <label htmlFor="phone">Phone No.</label>
                            <input id="phone" type="text" className="form-control" />
                        </div>
                    }
                    <div className="form-group">
                        <label htmlFor="email">Password</label>
                        <input id="password" type="password" className="form-control" />
                    </div>
                    {props.label === "Login" &&
                        <div className="form-group d-md-flex justify-content-between">
                            <div className="form-check">
                                <input className="form-check-input" type="checkbox" id="rememberCheck" name="remember" />
                                <label className="form-check-label text-muted" htmlFor="rememberCheck">Remember Me</label>
                            </div>
                            <Link className="font-weight-bold text-underline" to={"/login"}>Forgot Password</Link>
                        </div>
                    }
                    <div className="form-group pt-4 ">
                        <button onClick={event => handleOnSubmit(event)} className="btn btn-block btn-danger francy-btn">
                            <span>{props.label}</span>
    
                        </button>
                    </div>
                </form>
            </div>
            <div className="col-md-6 pl-md-5 my-2">
                <img className="img-fluid" src={manWithLaptop} alt="" />
            </div>
        </div>
    )
}

export default LoginRegisterForm;