import React, { useState } from "react";
import Milestone from "./milestone";
import milestoneFakeData from "../../../data/ModuleDetail/fakeMilestoneData";


const CourseContent = () => {
    const [milestoneData] = useState(milestoneFakeData);
    return (
        <div className="course-content-component">
            <div className="container">
                <h4 className="mt-3 mb-3">Course Content</h4>
                {milestoneData.map((eachMilestone, index) => (
                    <div key={index}>
                        <Milestone
                            title={eachMilestone.name}
                            minute="55"
                            modulePart="1/7"
                        />
                    </div>
                ))}

            </div>
        </div>
    )
}

export default CourseContent;