import React, { useEffect, useState } from "react";
import CustomDialog from "../../Custom/customDialog";
import copyrightClaimImage from "../../../assets/images/copyright-claim.png";
import warningIcon from "../../../assets/images/icons/ic_warning.png";

const CopyrightNotice = (props) => {

    const [openDialog, setOpenDialog] = useState(false);

    const handleClickOpen = () => {
        setOpenDialog(true);
    };

    const handleClickClose = () => {
        setOpenDialog(false);
    }

    return (
        <div className="copyright-notice-component mb-4">
            <div className="mt-4">
                <img className="warning-icon mr-2" src={warningIcon} />
                <a className="copyright-warning-link" onClick={() => handleClickOpen()}>
                    Copyright warning
                </a>
            </div>
            <CustomDialog
                openDialog={openDialog}
                handleClickClose={handleClickClose}
            >
                <img className="copyright-image img-fluid" src={copyrightClaimImage} />
            </CustomDialog>
        </div>
    )
}

export default CopyrightNotice;